import React from "react";
import PropTypes from "prop-types";
import cx from "classnames";
import * as styles from "./AppHeader.module.scss";

const AppHeader = ({ title, icon, theme, tabs = [] }: any) => (
  <div
    className={cx(styles.appHeader, {
      [styles.withTabs]: tabs.length,
      [styles.darkMode]: theme === "dark",
    })}
  >
    <div className={styles.titleGrid}>
      <div className="cds--row">
        <div className="cds--col-lg-12">
          <h3 id="page-title" className={styles.text}>
            <span className={styles.iconSpan}>{icon}</span>
            <span className={styles.titleSpan}>{title}</span>
          </h3>
        </div>
      </div>
    </div>
  </div>
);

AppHeader.propTypes = {
  /**
   * Specify the title for the page
   */
  title: PropTypes.node,
  icon: PropTypes.element,
};

export default AppHeader;
