import React from "react";
import PropTypes from "prop-types";
import cx from "classnames";
import * as styles from "./PageHeader.module.scss";

const PageHeader = ({ title, theme, tabs = [] }: any) => (
  <div
    className={cx(styles.pageHeader, {
      [styles.withTabs]: tabs.length,
      [styles.darkMode]: theme === "dark",
    })}
  >
    <div className={styles.titleGrid}>
      <div
        className="cds--row"
        style={{ marginRight: "unset", marginLeft: "-1rem" }}
      >
        <div className="cds--col-lg-12">
          <h2 id="page-title" className={styles.text}>
            {title}
          </h2>
        </div>
      </div>
    </div>
  </div>
);

PageHeader.propTypes = {
  /**
   * Specify the title for the page
   */
  title: PropTypes.node,
};

export default PageHeader;
